import React from "react";
import "../css/Connect.css"
import {PopupActions, DialogType} from "react-custom-popup";

export default function Connect(){
    const emailPopup=()=>{
            PopupActions.showToast({
              text: 'Email Copied',
              type: DialogType.INFO,
              timeoutDuration: 10000,
              showProgress: true,
              progressColor: 'black',
              containerStyle: { background: 'darkgray' },
            })
    }
    return(
        <div className='setUp'>
            <div className='all_content_Connect'>
                <p className='Title_connect'>Let's Connect</p>
                <a><p className= 'hover' onClick={() => {navigator.clipboard.writeText('fernandesdylan100@gmail.com'); emailPopup()}}style={{height:40,fontWeight:300}}>Email</p></a><br/>
                <a href ='https://www.instagram.com/dylan_fernz/' target='_blank'><p className= 'hover'style={{height:40,fontWeight:300}}>Instagram</p></a><br/>
                <a><p className= 'hover'style={{height:40,fontWeight:300}}>LinkedIn</p></a><br/>
                
                
            </div>
        </div>
    )
}