import React from "react";
import HoverVideoPlayer from 'react-hover-video-player';
import './css/VideoPlayer.css'
import video1 from '../../../assets/video/video1.mp4'
import video2 from '../../../assets/video/video2.mov'
import video3 from '../../../assets/video/video3.mov'
import video4 from '../../../assets/video/video4.mp4'


export default function VideoPlayer() {
    const style = {
        color: 'white',
        fontSize: '1vw'
    }
    const goToPage= () =>{
        window.history.back();
    }
    return (
        <div className='setUp'>
            <div className='all_content_video_player'>
                <div className='big_title' style={{ paddingTop: '10vh', paddingBottom: '0vh', position: 'relative', bottom: '10vh' }}>ANIMATION</div>
                <div style={{ paddingTop: '5vh', paddingBottom: '5vh' }}><p style={{ fontSize: '2rem', textTransform: 'uppercase' }}>Channel Identifier</p>
                    <p>A Frame by Frame animation done for the channel  NICKELODEON </p></div>

                <HoverVideoPlayer
                    className='video_1'
                    videoSrc={video1}
                    muted={false}
                    controls={true}

                />
                <div style={{ paddingTop: '20vh', paddingBottom: '5vh' }}><p style={{ fontSize: '2rem', textTransform: 'uppercase' }}>Red Dot Animation</p>
                    <p>A group project done by the CG students of our college,</p>
                    <p>our brief was visualising a  RED DOT  through animation media.</p></div>

                <HoverVideoPlayer
                    className='video_2'
                    videoSrc={video2}
                    controls={true}
                    muted={true}

                />
                <div style={{ paddingTop: '20vh', paddingBottom: '5vh' }}><p style={{ fontSize: '2rem', textTransform: 'uppercase' }}>Animated Movie Scene</p>
                    <p>A animated scene done from the movie  DR . STRANGE . </p>
                    <p> The animation is done with a mix of CG/VFX and traditional rendering of the character</p></div>

                <HoverVideoPlayer
                    className='video_3'
                    videoSrc={video3}
                    controls={true}

                />
                <div style={{ paddingTop: '20vh', paddingBottom: '5vh' }}><p style={{ fontSize: '2rem', textTransform: 'uppercase' }}>Communication Poster Animation</p>
                    <p>A Motion Graphics Video expressing </p>
                    <p>the psychedelic colours of the artist  MILTON GLASER  and his work </p>
                </div>


                <HoverVideoPlayer
                    className='video_4'
                    videoSrc={video4}
                    controls={true}


                />
                <div className='navigator_footer' style={style}>
                    <a href="#/" className='nextProject'>
                        <p className='hover' style={{ height: 30, textAlign: 'center' }}>Back To Homepage</p>
                    </a><br />
                    <a className='back' href='#/Graphic_Design/'>
                        <p className='hover' style={{ height: 30, textAlign: 'center' }}>Previous Project</p>
                    </a>
                </div>
            </div>

        </div>
    )
}